import React, { useEffect, useState } from "react";
import { v4 as uuid } from "uuid";
import { useLocation,useParams } from "react-router";

const ModificarScreen = ({ history }) => {

    const { id } = useParams();
    console.log(id);
    var local =[];

    local = JSON.parse(localStorage.getItem('participantes'));
    // var p;
    const participante = local.filter(el => el != null)
    const p = participante.find((p) => p.id === id)
    // console.log(p);

    const [data, setData] = useState({ nombre: p.nombre, apellidos: p.apellidos, email: p.email, twitter: p.twitter, avatar: p.avatar });
  
    const { nombre, apellidos, email, twitter} = data;

    const [checked, setChecked] = React.useState(false);

    const [avatar, setAvatar] = React.useState(0);

    //marcamos el radio del avatar
    useEffect(() => {
        switch (p.avatar) {
            case 1:
                document.getElementById("avatar1").checked = true;
                break;
            case 2:
                document.getElementById("avatar2").checked = true;
                break;
            case 3:
                document.getElementById("avatar3").checked = true;
                break;
            default:
                break;
        }
    }, [])
   //Definimos la funcion handleChange //uso el metodo de abajao para agregar los valores
   const handleChange = (e) => {
    setData({
      ...data,
      [e.target.name]: e.target.value,
    });
  };

  const handleClick = () => {
    //invocamos el dispatch, todos los dispatch van a tener referencia directa con el reducer a ejecutar, debemos pasarle un action
    
    if(checked){

        if (nombre && apellidos && email && twitter &&(avatar!=0 || p.avatar!=0)) {
      
            p.nombre = data.nombre;
            p.apellidos = data.apellidos;
            p.email = data.email;
            p.twitter = data.twitter;
            p.avatar = avatar;
            
            localStorage.setItem("participantes", JSON.stringify(local));
            setData({ nombre: "", apellidos: "", email: "", twitter: "" , avatar:0});
            handleLista()
          } else {
            alert('Debes completar todos los campos');
          }
      
    }else{
        alert('Debes aceptar los terminos y condiciones');
    }
  };
    
    const handleLista = () => {
        history.push("/listaparticipantes")
    }

    return (
        <div className="container">
            <h1>Modificar de participante</h1>
            <hr />
            <label className="mx-1 d-grid gap-2">
                Nombre: {" "}
                <input onChange={handleChange}
                    name="nombre"
                    value={nombre}
                    type="text"
                    className="form-control"
                    autoComplete="off" />
            </label>
            <label className="mx-1 d-grid gap-2">
                Apellidos: {" "}
                <input
                    onChange={handleChange}
                    name="apellidos"
                    value={apellidos}
                    type="text"
                    className="form-control"
                    autoComplete="off" />
            </label>
            <label className="mx-1 d-grid gap-2">
                Email: {" "}
                <input
                    onChange={handleChange}
                    name="email"
                    value={email}
                    type="text"
                    className="form-control"
                    autoComplete="off" />
            </label>
            <label className="mx-1 d-grid gap-2">
                Twitter: {" "}
                <input onChange={handleChange}
                    name="twitter"
                    value={twitter}
                    type="text"
                    className="form-control"
                    autoComplete="off" />
            </label>
            <br />
            <div className="row">
                <div className="col-4">
                    <label>
                        <center>
                            <img src="https://i.pinimg.com/736x/4b/1a/41/4b1a41edd5c8f7cf44834cd24f9b1cc7.jpg" alt="Avatar 1" width="250px" height="250px"/>
                            <br />
                            <input type="checkbox"onChange={() => setAvatar(1)} id="avatar1" name="imagge"/>
                            avatar 1
                        </center>
                    </label>
                </div>
                <div className="col-4">
                    <label>
                        <center>
                            <img src="https://i.pinimg.com/originals/cc/8c/b4/cc8cb40099d7e044369ececfbc713a99.jpg" alt="Avatar 2" width="250px" height="250px" />
                            <br />
                            <input type="checkbox"onChange={() => setAvatar(2)} id="avatar2" name="imagge"/>
                            avatar 2
                        </center>
                    </label>
                </div>
                <div className="col-4">
                    <label>
                        <center>
                            <img src="https://image-aws-us-west-2.vsco.co/79af08/182455082/5f3db7a18cbbf07672000007/vsco5f3db7ab05e97.jpg" alt="Avatar 3" width="250px" height="250px"/>
                            <br />
                            <input type="checkbox"onChange={() => setAvatar(3)} id="avatar3" name="imagge"/>
                            avatar 3
                        </center>
                    </label>
                </div>
            </div>
            <label className="">
                <input type="checkbox"
                    onChange={() => setChecked(true)}
                />     Lei y acepto los terminos y condiciones
            </label>
            <div className="mx-1 d-grid gap-2">
                <button onClick={handleClick} className="btn btn-success mt-2">Guardar</button>
            </div>
            <br />
            <br />
        </div>
    );
};

export default ModificarScreen;