import React from 'react'
import {Link} from 'react-router-dom'

const Card = ({nombre,apellidos, twitter, email,id,avatar}) => {
   
    let path="";

    switch (avatar) {
        case 1:
            path="https://i.pinimg.com/736x/4b/1a/41/4b1a41edd5c8f7cf44834cd24f9b1cc7.jpg";
            break;
        case 2:
            path="https://i.pinimg.com/originals/cc/8c/b4/cc8cb40099d7e044369ececfbc713a99.jpg";
            break;
        case 3:
            path="https://image-aws-us-west-2.vsco.co/79af08/182455082/5f3db7a18cbbf07672000007/vsco5f3db7ab05e97.jpg";
            break;
        default:
            path="https://i.pinimg.com/originals/77/a2/52/77a25255f8a124fbd17502c5e74e1edc.jpg";
            break;
    }

    return (
        <div className='text-center p-5'>
            <img className="img-fluid rounded-pill" src={path} alt="Avatar" height="250px" width="250px"/>
                <h3 className="">{nombre} {apellidos}</h3>
                <h3 className="">
                    <a href={`https://twitter.com/@${twitter}`}>
                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-twitter" viewBox="0 0 16 16">
                            <path d="M5.026 15c6.038 0 9.341-5.003 9.341-9.334 0-.14 0-.282-.006-.422A6.685 6.685 0 0 0 16 3.542a6.658 6.658 0 0 1-1.889.518 3.301 3.301 0 0 0 1.447-1.817 6.533 6.533 0 0 1-2.087.793A3.286 3.286 0 0 0 7.875 6.03a9.325 9.325 0 0 1-6.767-3.429 3.289 3.289 0 0 0 1.018 4.382A3.323 3.323 0 0 1 .64 6.575v.045a3.288 3.288 0 0 0 2.632 3.218 3.203 3.203 0 0 1-.865.115 3.23 3.23 0 0 1-.614-.057 3.283 3.283 0 0 0 3.067 2.277A6.588 6.588 0 0 1 .78 13.58a6.32 6.32 0 0 1-.78-.045A9.344 9.344 0 0 0 5.026 15z"></path>
                        </svg>
                        @{twitter}
                    </a>
                    <br />
                    <br />
                    <h3>Mail: {email}</h3>
                    <Link className="btn btn-warning" to={`/modificarScreen/${id}`}>
                        Modificar
                    </Link>
                </h3>
        </div>
    )
}

export default Card
