import React, { useEffect, useState } from "react";
import { v4 as uuid } from "uuid";
import { useLocation } from "react-router";
import queryString from "query-string";
import { Participantes } from "../models/Participantes";
import Card from "../components/Card.jsx";


const RegistroScreen = ({ history }) => {

    var arreglo=[]
    var consulta=JSON.parse(localStorage.getItem('participantes'))
    if (consulta==null) {
        arreglo.push(consulta)
    }else{
        
    arreglo=consulta
    }


    const [participantes, setParticipantes] = useState({nombre: "", apellidos: "", email: "", twitter: "", avatar:0});

    const { nombre, apellidos, email, twitter} = participantes

    const [checked, setChecked] = React.useState(false);

    const [avatar, setAvatar] = React.useState(0);

    // Definimos la función handleChange
    const handleChange = (e) => {
        e.preventDefault();
        setParticipantes({
            ...participantes,
            [e.target.name]: e.target.value,
        })
    }

    const actionAdd = {
        id: uuid(),
        nombre,
        apellidos,
        email,
        twitter,
        avatar
    }
    const handleClick = () => {

        if(checked){
            if (nombre && apellidos && email && twitter &&(avatar!=0)) {
                arreglo.push(actionAdd)
                localStorage.setItem("participantes", JSON.stringify(arreglo))
                setParticipantes( {
                    nombre: "",
                    apellidos: "",
                    email: "",
                    twitter: "",
                    avatar:0
                });
                handleLista()
            }else {
                alert('Debes completar todos los campos');
              }
        }else{
            alert("Acpeta los terminos y condiciones");
        }
    }

    const handleLista = () => {
        history.push("/listaparticipantes")
    }

    return (
        <div className="container">
            <h1>Registro de participante</h1>
            <hr />
            <label className="mx-1 d-grid gap-2">
                Nombre: {" "}
                <input onChange={handleChange}
                    name="nombre"
                    value={nombre}
                    type="text"
                    className="form-control"
                    autoComplete="off" />
            </label>
            <label className="mx-1 d-grid gap-2">
                Apellidos: {" "}
                <input
                    onChange={handleChange}
                    name="apellidos"
                    value={apellidos}
                    type="text"
                    className="form-control"
                    autoComplete="off" />
            </label>
            <label className="mx-1 d-grid gap-2">
                Email: {" "}
                <input
                    onChange={handleChange}
                    name="email"
                    value={email}
                    type="text"
                    className="form-control"
                    autoComplete="off" />
            </label>
            <label className="mx-1 d-grid gap-2">
                Twitter: {" "}
                <input onChange={handleChange}
                    name="twitter"
                    value={twitter}
                    type="text"
                    className="form-control"
                    autoComplete="off" />
            </label>
            <br />
            <div className="row">
                <div className="col-4">
                    <label>
                        <center>
                            <img src="https://i.pinimg.com/736x/4b/1a/41/4b1a41edd5c8f7cf44834cd24f9b1cc7.jpg" alt="Avatar 1" width="250px" height="250px"/>
                            <br />
                            <input type="checkbox"onChange={() => setAvatar(1)}/>
                            avatar 1
                        </center>
                    </label>
                </div>
                <div className="col-4">
                    <label>
                        <center>
                            <img src="https://i.pinimg.com/originals/cc/8c/b4/cc8cb40099d7e044369ececfbc713a99.jpg" alt="Avatar 2" width="250px" height="250px" />
                            <br />
                            <input type="checkbox"onChange={() => setAvatar(2)}/>
                            avatar 2
                        </center>
                    </label>
                </div>
                <div className="col-4">
                    <label>    
                        <center>
                            <img src="https://image-aws-us-west-2.vsco.co/79af08/182455082/5f3db7a18cbbf07672000007/vsco5f3db7ab05e97.jpg" alt="Avatar 3" width="250px" height="250px"/>
                            <br />
                            <input type="checkbox"onChange={() => setAvatar(3)}/>
                            avatar 3
                        </center>
                    </label>
                </div>
            </div>
            <label className="">
                <input type="checkbox"
                    onChange={() => setChecked(true)}
                />     Lei y acepto los terminos y condiciones
            </label>
            <div className="mx-1 d-grid gap-2">
                <button onClick={handleClick} className="btn btn-success mt-2">Guardar</button>
            </div>
            <br />
            <br />
        </div>
    );
};
export default RegistroScreen;
